      // http://en.wikipedia.org/wiki/Quadratic_equation
      // h1 = x1,
      // k1 = (y1+directrix)/2,
      // h2 = x2,
      // k2 = (y2+directrix)/2,
      // p1 = k1-directrix,
      // a1 = 1/(4*p1),
      // b1 = -h1/(2*p1),
      // c1 = h1*h1/(4*p1)+k1,
      // p2 = k2-directrix,
      // a2 = 1/(4*p2),
      // b2 = -h2/(2*p2),
      // c2 = h2*h2/(4*p2)+k2,
      // x = (-(b2-b1) + Math.sqrt((b2-b1)*(b2-b1) - 4*(a2-a1)*(c2-c1))) / (2*(a2-a1))
